.navbar {
    display: flex;
    align-items: center;
    justify-content: center;
    position: sticky;
    top: 0;
    background:#ffcc00;
    overflow: hidden;
    z-index: 1;
}
.nav-item a:link, a:visited {
    color: black;
    height: 100%;
    padding: 14px 25px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    margin-left: 10px;
  }
  
 .nav-item a:hover,.nav-item a:active {
    color: white;
  }
 
  .navbar-nav{
    margin: auto;
  }
  .navbar-toggler{
    color: black;
  }
  .navbar span{
    display: flex;
    justify-content: center;
    text-align: center;
  }