.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
main{
  display: flex;
  flex-direction: column;
}
.mycontainer{
  background:rgba(192, 192, 171, 0.431);
  display: flex;
  flex-direction: column;
  margin-left: 40px;
  margin-right: 40px;
}
@media (max-width:768px) {
  .mycontainer{
    margin-left: 0px;
    margin-right: 0px;
  }
}
