.education {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
  margin-left: 50px;
  margin-right: 50px;
  justify-content: center;
}
.education h4 {
  text-align: center;
  display: flex;
  width: 140px;
  margin: auto;
  justify-content: center;
  flex-direction: column;
  padding: 10px;
  border: 1px solid blue;
}
.educontainer {
  display: flex;
  flex-direction: row; 
}
.content {
  display: flex;
  flex-direction: column;
  margin-top: 30px;
}
.smargin {
  margin-left: 50px;
}
.school {
  padding: 10px 10px;
  background: white;
  /* border-top:4px solid blue; */
  display: flex;
  flex-direction: column;
}
.educontainer span {
  padding: 0px 0px;
  border-top: 4px solid blue;
  width: 540px;
  margin-bottom: 0;
  border-radius: 5px;
}
.icon {
  padding: 7px 7px;
  background: blue;
  width: 50px;
  height: 50px;
  color: white;
  margin: auto;
  border-radius: 25px;
  text-align: center;
}
@media (max-width:768px){
    .educontainer{
        flex-direction: column;
    }
   .content.smargin{
     margin-left: 0px;
   }
}
