.aboutme{
    
    background-color:white;
    display: flex;
    flex-direction: row;
    margin-top: 50px;
    
    margin-left: 50px;
    margin-right: 50px;

}

img{
    
    height: 280px;
    width: 280px;
    justify-content: center;
    text-align: center;
    margin: auto;
    display: flex;
    margin-top: 10px;
}
.leftSide{
    display: flex;
    flex-direction: column;
    margin-left: 50px;
    margin-top: 30px;
    width: 50%;
    justify-content: center;
}
.rightSide{
    display: flex;
    
    margin-top: 30px;
    margin-right: 50px;
    margin-left: 50px;
    justify-content: center;
    flex-direction: column;
    text-align: left;

}
.rightSide h2,h6{
    justify-content: center;
    text-align: center;
    display: flex;
}
.picutre{
    width: 50%;
    height: 40px;
    display: flex;
    
}
.info{
    display: flex;
    flex-direction: row;
}

.infoLeft{
    width: 50%;
    display: flex;
    flex-direction: column;
    
    
}
.infoRight{
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-left: 20px;
  
    
}
.download{
    margin-top: 40px;
    justify-content: center;
    text-align: center;
    margin: auto;
}
.download button{
    background: #ffcc00;
    border-radius: 10px;
}
.linkbar{
    margin-top: 10px;
    margin:auto;
}
.linkbar a{
    color: white;
}
@media (max-width :768px){
    .aboutme{
        display: flex;
        flex-direction: column;
    }
    .picutre{
        width: 50%;
        height: 40px;
        display: flex;
        justify-content: center;
        border-radius: 50%;
    }
    .leftSide{
        width: 100%;
        margin: auto;
    }
    .rightSide{
        width: 90%;
        margin-left: 30px;
        margin-right: 30px;
    }
}