.project {
    display: flex;
    flex-direction: column;
    margin-top: 30px;
    margin-left: 50px;
    margin-right: 50px;
    justify-content: center;
    margin-bottom: 30px;
  }
  /* .school a{
    text-decoration: none;
    background-color: rgb(0, 2, 128);
    padding: 10px 10px;
    
  } */
  .project h4 {
    text-align: center;
    display: flex;
    width: 140px;
    margin: auto;
    justify-content: center;
    flex-direction: column;
    padding: 10px;
    border: 1px solid blue;
  }
  .procontainer {
    display: flex;
    flex-direction: row;
    margin-top: 30px;
    justify-content: center;
    text-align: center;
  }
  .contentleft {
    display: flex;
    flex-direction: column;
    width: 40%;
    text-align: left;
  }
  .contentleft a{
    margin-left: 10px;
  }
  

  .contentright {
    display: flex;
    flex-direction: column;
    margin-left: 240px;
    width: 40%;
    text-align: left;
  }
  .smargin {
    margin-left: 50px;
  }
  .school {
    padding: 10px 10px;
    background: white;
    /* border-top:4px solid blue; */
    display: flex;
    flex-direction: column;
  }
  .educontainer span {
    padding: 0px 0px;
    border-top: 4px solid blue;
    width: 540px;
    margin-bottom: 0;
    border-radius: 5px;
  }
  .icon {
    padding: 7px 7px;
    background: blue;
    width: 50px;
    height: 50px;
    color: white;
    margin: auto;
    border-radius: 25px;
    text-align: center;
  }
  @media (max-width:768px){
      .procontainer{
          flex-direction: column;
      }
      .contentleft{
         width: 100%;
      }
      .contentright{
        width: 100%;
        margin-left: 0px;
        margin-top: 30px;
      }
  }
  